<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="物流公司名称"
          size="small"
          style="width: 140px"
          v-model="searchParams.expressCorpName"
          clearable
        ></el-input>

        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'0px 0px'}"
    >
      <el-table-column align="center" prop="expressCorpName" label="公司名称"></el-table-column>
      <el-table-column align="center" prop="expressCorpId" label="拼音"></el-table-column>
      <el-table-column
        align="center"
        prop="expressStatus"
        label="状态"
        :formatter="expressStatusFormatter"
      ></el-table-column>
      <el-table-column align="center" label="操作">
        <div slot-scope="s">
          <el-button type="primary" size="small" @click="edit(s.row)">编辑</el-button>
          <el-button
            type="info"
            size="small"
            @click="changeStatus(s.row,0)"
            v-if="s.row.expressStatus==1"
          >禁用</el-button>
          <el-button
            type="success"
            size="small"
            @click="changeStatus(s.row,1)"
            v-if="s.row.expressStatus==0"
          >恢复正常</el-button>
        </div>
      </el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getExpressComapnyList"
      ref="pagination"
      @returnData="returnData"
    />
    <ExpressCompanyEdit
      :title="titie"
      ref="edit"
      :showEditDialog="showEditDialog"
      @close="showEditDialog = false"
      @success="refresh"
    />
  </div>
</template>

<script>
import {
  getExpressComapnyList,
  editExpressComapny
} from "@/api/logistics/logisticsManage";
import { ExpressStatusEnum } from "@/utils/Immutable";
import { resetObject } from "@/utils/common";
import ExpressCompanyEdit from "./ExpressCompanyEdit";
export default {
  components: { ExpressCompanyEdit },
  data() {
    return {
      searchParams: {
        expressCorpName: "",
        goodsName: ""
      },
      getExpressComapnyList: getExpressComapnyList,
      titie: "",
      total: 10,
      showEditDialog: false,
      tableData: [],
      expressStatusEnum: ExpressStatusEnum
    };
  },
  methods: {
    expressStatusFormatter(a, b, v) {
      return this.expressStatusEnum[v];
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    async changeStatus(v, status) {
      let data = await editExpressComapny({
        id: v.id,
        expressStatus: status
      });
      if (data.code != 0) {
        this.$message.error(data.msg);
        return;
      }
      this.refresh();
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    },
    returnData(data) {
      this.tableData = data.map(v => {
        v.commentPicObj = v.commentPic ? JSON.parse(v.commentPic) : [];
        return v;
      });
    },
    edit(row) {
      this.titie = "编辑";
      this.showEditDialog = true;
      this.$refs.edit.update(row.id, row);
    }
  }
};
</script>
